import React, { useState } from "react";
import { Button, Nav, NavItem } from "reactstrap";
import { Link, useLocation } from "react-router-dom";

const navigation = [
  { // home
    title: "home",
    href: "/home",
    icon: "bi bi-house",
  },
  { // Beacon Data
    title: "Beacon Data",
    href: "/beacon_data/beaconlist",
    icon: "bi bi-bar-chart-fill",
    subitems: [
      {
        title: "List View",
        href: "/beacon_data/beaconlist",
        icon: "bi bi-list",
      },
      {
        title: "Map View",
        href: "/beacon_data/beaconmap",
        icon: "bi bi-geo-alt-fill",
      },
    ],
  },
  { // Tracking Data
    title: "Tracking Data",
    href: "/tracking_data/trackinglist",
    icon: "bi bi-bar-chart",
    subitems: [
      {
        title: "List View",
        href: "/tracking_data/trackinglist",
        icon: "bi bi-list",
      },
      {
        title: "Map View",
        href: "/tracking_data/trackingmap",
        icon: "bi bi-geo-alt-fill",
      },
    ],
  },
  { // Alert
    title: "Alert",
    href: "/alerts",
    icon: "bi bi-bell",
  },
  {
    title: "About",
    href: "/about",
    icon: "bi bi-people",
  },
];

const Sidebar = () => {
  const [isTrackingDataOpen, setTrackingDataOpen] = useState(false);
  const [isBeaconDataOpen, setBeaconDataOpen] = useState(false);
  const toggleTrackingData = () => {
    setTrackingDataOpen(!isTrackingDataOpen);
  };
  const toggleBeaconData = () => {
    setBeaconDataOpen(!isBeaconDataOpen);
  };

  const showMobilemenu = () => {
    document.getElementById("sidebarArea").classList.toggle("showSidebar");
  };
  let location = useLocation();


  return (
    <div className="bg-dark" >
      <div className="d-flex">
        <Button
          color="white"
          className="ms-auto text-white d-lg-none"
          onClick={() => showMobilemenu()}
        >
          <i className="bi bi-x"></i>
        </Button>
      </div>
      <div className="p-3 mt-2">
        <Nav vertical className="sidebarNav" >
          {navigation.map((navi, index) => (
            <NavItem key={index} className="sidenav-bg">
              {navi.subitems ? (
                <div>
                  <div
                    onClick={
                      navi.title === "Beacon Data"
                        ? toggleBeaconData
                        : navi.title === "Tracking Data"
                          ? toggleTrackingData
                          : null
                    }
                    className="nav-link py-3"
                    style={{ cursor: "pointer", fontWeight: "bold" }}
                  >
                    <i className={navi.icon}></i>
                    <span className="ms-3 d-inline-block" style={{ marginRight: "20px" }}>{navi.title}</span>
                    {navi.title === "Beacon Data" && (
                      <i className={isBeaconDataOpen ? "bi bi-caret-up-fill" : "bi bi-caret-down-fill"}></i>
                    )}
                    {navi.title === "Tracking Data" && (
                      <i className={isTrackingDataOpen ? "bi bi-caret-up-fill" : "bi bi-caret-down-fill"}></i>
                    )}
                  </div>
                  {navi.title === "Beacon Data" && isBeaconDataOpen && (
                    <Nav vertical className="ml-4">
                      {navi.subitems.map((subitem, subindex) => (
                        <NavItem key={subindex} className="sidenav-bg" >
                          <Link
                            to={subitem.href}
                            className={
                              location.pathname === subitem.href
                                ? "active nav-link py-1"
                                : "nav-link py-1"
                            }
                          >
                            <i className={subitem.icon}></i>
                            <span className="ms-3 d-inline-block">
                              {subitem.title}
                            </span>
                          </Link>
                        </NavItem>
                      ))}
                    </Nav>
                  )}
                  {navi.title === "Tracking Data" && isTrackingDataOpen && (
                    <Nav vertical className="ml-4">
                      {navi.subitems.map((subitem, subindex) => (
                        <NavItem key={subindex} className="sidenav-bg">
                          <Link
                            to={subitem.href}
                            className={
                              location.pathname === subitem.href
                                ? "active nav-link py-1"
                                : "nav-link py-1"
                            }
                          >
                            <i className={subitem.icon}></i>
                            <span className="ms-3 d-inline-block">
                              {subitem.title}
                            </span>
                          </Link>
                        </NavItem>
                      ))}
                    </Nav>
                  )}
                </div>
              ) : (
                <Link
                  to={navi.href}
                  className={
                    location.pathname === navi.href
                      ? "active nav-link py-3"
                      : "nav-link py-3"
                  }
                  style={{ fontWeight: "bold" }}
                >
                  <i className={navi.icon}></i>
                  <span className="ms-3 d-inline-block">{navi.title}</span>
                </Link>
              )}
            </NavItem>
          ))}
        </Nav>
      </div>
    </div>
  );
};

export default Sidebar;